import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';


interface User { }


@Component({
  selector: 'app-new',
  templateUrl: './new.component.html',
  styleUrls: ['./new.component.css'],

})
export class NewComponent implements OnInit {
  finished = false;
  page = 1;
  mangUser = [];
  constructor(private http: Http) {
    this.http.get('http://localhost:3000/api/new/1').toPromise()
      .then(res => res.json())
      .then(resJson => resJson.forEach(e => this.mangUser.push(e)))
  }

  onScroll() {
    console.log('=========scrolling...===========')
    this.finished = false;
    this.getUsers()
  }
  
  private getUsers() {
    this.page = this.page + 1
    console.log(this.finished + ' ==============finished ')
    if (!this.finished) {
      this.http.get('http://localhost:3000/api/new/' + this.page).toPromise()
        .then(res => res.json())
        .then(resJson => {
          resJson.forEach(e => {
            this.mangUser.push(e)
            console.log('added ' + e.from.name)
          })
          console.log('xong doi do')
          this.finished = true;
          console.log(this.page + 'getUser')
        })
    } else {
      return
    }
  }
  ngOnInit() {
  }
}



